import React, { useState, useEffect, useRef, useCallback } from 'react';
import './App.css';
import College from './components/College/College';
// import './Modal.css';
import Main from './components/Main/Main';
import { BrowserRouter } from 'react-router-dom';
import { Switch, Route, Redirect } from 'react-router-dom';
import Student from './components/Students/Student';

import { createBrowserHistory } from 'history';

import * as Sentry from '@sentry/react';
import { Integrations } from '@sentry/tracing';
import { NewProfile } from './components/NewProfile/NewProfile';
import UtilityBoard from './components/UtilityBoard/UtilityBoard';
import { Company } from './components/Companies/Company';
import Settings from './components/Settings/Settings';
import Home from './components/Hiring-Challenges/Home';
import JobContainer from './components/Jobs/Jobs';
import ForYou from './components/ForYou/ForYou';
import Openings from './components/ForYou/Openings';
import { Description } from './components/ForYou/Description';

const history = createBrowserHistory();


Sentry.init({
  integrations: [
    new Integrations.BrowserTracing({
      // Can also use reactRouterV3Instrumentation or reactRouterV4Instrumentation
      routingInstrumentation: Sentry.reactRouterV5Instrumentation(history),
    }),
  ],

  // We recommend adjusting this value in production, or using tracesSampler
  // for finer control
  tracesSampleRate: 1.0,
});

function App() {
  const [userData, setUserData] = useState({});
  
  return (
    <>
      <BrowserRouter history={history} >
        <Switch>
          <Route exact path="/" render={(props) => <Main {...props} setUserData={setUserData} />}/>
          <Route exact path="/dashboard/profile" render={(props) => (<NewProfile {...props} user={userData} setUserData={setUserData}/>)}/>
          <Route exact path="/dashboard/utilityboard" component={UtilityBoard} />
          <Route exact path="/dashboard/companies" component={Company} />
          <Route exact path="/dashboard/settings" render={(props) => (<Settings {...props} user={userData} setUserData={setUserData}/>)}/>
          <Route exact path="/dashboard/hiring_challenges" component={Home}/>
          <Route exact path="/dashboard/jobs_openings" component={JobContainer}/>
          <Route exact path="/dashboard/for_you" component={ForYou}/>
          <Route exact path="/dashboard/for_you/openings" component={Openings}/>
          <Route exact path="/dashboard/for_you/openings/description" component={Description}/>
          <Route exact path="/college" component={College}/>
          <Route exact path="/student" component={Student}/>
        </Switch>
      </BrowserRouter>
    </>
  );
}

export default App;
